import React , {useState } from 'react'
import data from './data'
import './style.css'

function Main() {
 const [index , setIndex] = useState(0)
 const [lorem , setLorem]=useState([]);
 const handelSupmit = (e)=>{
  e.preventDefault()
  let value = parseInt(index);
  if(index <= 0){
    value=1;
  }
  if(index > 8){
    value=8;
  }
  setLorem(data.slice(0,value))
 }
 return (
  <div className="container">
   <h1>TIRED OF BORING LOREM IPSUM?</h1>
   <div className="formcontainer">
    <form onSubmit={handelSupmit}>
     <label htmlFor="generate">Paragraphs</label>
     <input type="number" id='generate' name='generate' value={index} onChange={(e)=> setIndex(e.target.value)}/>
     <button type='submit' className='btn'>generate</button>
    </form>
    <div className="loremContainer">
     {
      lorem.map((lorem , index)=>{
        return(
         <p key={index}>{lorem}</p>
        )
      })
     }
    </div>
   </div>
  </div>
 )
}

export default Main
